 *
 * Prefix your imports with `~` to grab from node_modules/
 * @see https://github.com/webpack-contrib/sass-loader#imports
 */
/** Import theme styles */
body {
  position: relative; }

h1 small {
  font-size: 60%;
  color: #adb5bd; }

.dropdown-menu {
  z-index: 1010 !important; }

.page-header {
  border-bottom: 1px solid #dee2e6; }

.list-group-content-links a,
.content-links a {
  color: #212529 !important;
  text-decoration: none; }

.list-group-content-links .card:hover,
.content-links .card:hover {
  background: #f8f9fa; }

.list-group-content-links h3,
.list-group-content-links h5,
.content-links h3,
.content-links h5 {
  color: #f26522 !important; }

.show-stuck {
  transition: all 0.2s ease-in-out;
  opacity: 0;
  visibility: hidden; }
  .js-is-sticky .show-stuck,
  .js-is-stuck .show-stuck {
    opacity: 1;
    visibility: visible; }

.flag-icon::before {
  content: '';
  margin-right: 0.5rem;
  height: 1.5rem;
  width: 1.5rem;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 1.5rem; }

.flag-icon.flag-fr::before {
  background-image: url(../images/flags/france.svg); }

.flag-icon.flag-en::before {
  background-image: url(../images/flags/usa.svg); }

.flag-icon.flag-es::before {
  background-image: url(../images/flags/mexico.svg); }

.flag-icon.flag-zh-hant::before {
  background-image: url(../images/flags/china-2.svg); }

.flag-icon.flag-de::before {
  background-image: url(../images/flags/germany.svg); }

.comment-list ol {
